import React from "react"
import ShellDecorator from "../../components/common/shelldecorator/shelldecorator"
import DocLayout from "../../components/docs/layout/doclayout"
import ExtLink from "../../components/common/extlink/extlink"
import { Image } from "react-bootstrap"
import Codeblock from "../../components/common/codeblock/codeblock"
import YoutubeWrap from "../../components/common/youtubewrap/youtubewrap"

const QuickStart = () => {
  const pageTitle = "Quick Start"

  return (
    <DocLayout section={ pageTitle }>
      <p>Having reliable VPN server to virtually travel the world and browse the Internet safely matters a lot nowadays.
      In this tutorial we will guide you through the process of setting up your own secure Virtaul Privite Network server using Docker. 
      For the sake of convinience and reliability we chose OpenVPN solution. It has few advantages over other providers, the most important one is its ubiquity 
      and pretty high security level.</p>
      <h2>Prerequisites</h2>
      <p>To get started you'll have to have a few things:</p>
      <ul>
        <li>Linux server</li>
        <li>Public IP address</li>
        <li>Docker</li>
      </ul>
      <h3>Linux server</h3>
      <p>There is a plethora of options on the market. You can go alog with your own hardware server or rent a Virtual Private Server (VPS) in the cloud,
      to name but few  providers: Amazon AWS, Digital Ocean, Google Cloud Services. Choose the one you like the most and leap forward.</p>
      <h3>Public IP address</h3>
      <p>Whichever server option you chose, you'll have to make sure your server has a publically available IP address assigned to it. 
      If you rent your server from a hosting provider, it should be possible to do it from the dashboard of your controll panel.</p>
      <h3>Docker</h3>
      <p>Though many hosting providers offer VPS solutions with preinstalled Docker or Kubernetes, sometimes you have to make this installation on your own. 
      If this is the case, you shouldn't be scared — the installation steps are easy and won't take much time.</p>
      <h2>Guideline steps</h2>
      <p>The next simple steps will help you to get started with docker-openvpn.</p>
      <h3>1. Run docker-openvpn</h3>
      <p>Copy'n'paste the below code snippet into you server console.</p>
      <ShellDecorator>
          <Codeblock language="shell">
{`docker run -it --rm --cap-add=NET_ADMIN \\
-p 1194:1194/udp -p 80:8080/tcp \\
-e HOST_ADDR=$(curl -s https://api.ipify.org) \\
--name dockovpn alekslitvinenk/openvpn
`}
        </Codeblock>
      </ShellDecorator>
      <br></br>
      <p><strong><span role="img">⚠️</span> Note:</strong> <span style={{fontStyle: 'italic', fontSize: '0.95em'}}>This snippet runs Dockovpn in attached mode, which means if you close your terminal window, container will be stopped. 
      To prevent this from happening, you first need to detach container from ssh session. Type </span> <span style={{fontStyle: 'italic', fontSize: '0.95em', fontWeight: "600"}}>Ctrl+P Ctrl+Q</span> </p>
      <h3>2. Get client config file</h3>
      <p>Wait until the previous step completes and you see the output similar to following:</p>
        <ShellDecorator>
          <Codeblock language="shell" copyBtn={ false }>
{`Sat Mar 7 08:45:32 2020 Config server started, download your 
client.ovpn config at http://81.198.169.234/
Sat Mar 7 08:45:32 2020 NOTE: After you download you client 
config, http server will be shut down!

`}
          </Codeblock>
        </ShellDecorator>
      <br></br>
      <p>Copy printed link and paste it in your browser's address bar, then press Enter. Config will be downloaded in a moment.</p>
      <h3>3. Feed downloaded config to your OpenVPN cleint</h3>
      <p>There are many client applicatins available for all platforms. In this tutorial we are going to use <ExtLink to="https://tunnelblick.net/">Tunnelblick</ExtLink> for Mac,
      but steps for any other client should be similar.</p>
      <figure>
        <Image src="/images/TunnelblickIcon.png" width="262" height="auto" alt="Tunnelblick application icon"/>
        <figcaption>Pic 1. Tunnelblick icon</figcaption>
      </figure>
      <figure>
        <Image src="/images/TunnelblickDesktop.png" width="700" height="auto" alt="Tunnelblick app in MacOS status bar"/>
        <figcaption>Pic 2. Tunnelblick app in MacOS status bar</figcaption>
      </figure>
      <p>It should be enough to just doubleclick <strong>client.ovpn</strong> file and it'll start getting installed.</p>
      <figure>
        <Image src="/images/ClientOvpn.png" width="200" height="auto" alt="Client config file"/>
        <figcaption>Pic 3. Client config file</figcaption>
      </figure>
      <figure>
        <Image src="/images/TunnelblickInstall.png" width="496" height="auto" alt="Config installation window"/>
        <figcaption>Pic 4. Config installation window</figcaption>
      </figure>
      <h3>4. Connect to your OpenVPN server</h3>
      <p>After client config installation is completed, you should be able to connect to your OpenVPN server from context menu of Tunnelblick application in status bar.</p>
      <figure>
        <Image src="/images/TunnelblickConnect.png" width="700" height="auto" alt="Select OpenVPN config to connect"/>
        <figcaption>Pic 5. Select OpenVPN config to connect</figcaption>
      </figure>
      <p>Establishing connection usually takes few seconds. After this is completed you'll see this window right below status bar.</p>
      <figure>
        <Image src="/images/TunnelblickSuccess.png" width="650" height="auto" alt="Connection successfully established"/>
        <figcaption>Pic 6. Connection successfully established</figcaption>
      </figure>
      <h2>Summary</h2>
      <p>In this short tutorial we've seen an example of quick launching of OpenVPN server in Docker. 
        The whole process, starting from the point when you enter docker run command in your server's terminal 
        to the moment when server is up and running and ready to acept connections, takes approximately 2 seconds. 
        Add to this couple of more seconds to import generated client configuration. We encourage you to further explore our <a href="/docs/">Documentation</a> and 
        get maxumum utility from your Dockovpn.</p> 
      <p>Additionally you can watch this video tutorial below showing in details the described process using Digital Ocean hosting provider.</p>
      <figure>
        <YoutubeWrap title="Dockovpn Video Tutorial" src="https://www.youtube.com/embed/y5Dwakc6hMs"/>
        <figcaption>Video 1. Docker OpenVPN tutorial</figcaption>
      </figure>
    </DocLayout>
  )
}

export default QuickStart