import React from "react"
import * as styleModule from "./youtubewrap.module.css"

const YoutubeWrap = ({ title, src }) => (
    <div className={ styleModule.videoContainer }>
        <iframe 
            title={ title } 
            src={ src } 
            frameborder="0" 
            allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
            allowfullscreen>
        </iframe>
    </div>
)

export default YoutubeWrap